<template>
  <Modal
    :active="showModal"
    :event-name="'select-product-variable-modal'"
    :full-screen="'nexus'"
    :custom-padding="'p-spacing-0'"
  >
    <SelectProductVariableForm
      v-if="selectOptions && product && !isNaN(parseInt(index))"
      :select-options="selectOptions"
      :index="index"
      :product="product"
    />
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
export default {
  components: {
    Modal,
    SelectProductVariableForm: () =>
      import("@/components/MarketResearch/SelectProductVariableForm.vue"),
  },
  data() {
    return {
      showModal: false,
      selectOptions: null,
      product: null,
      index: null,
    };
  },
  mounted() {
    this.$bus.$on("select-product-variable-modal", (data) => {
      if (data?.showModal) {
        this.showModal = data?.showModal;
        this.selectOptions = data?.selectOptions;
        this.index = data?.index;
        this.product = data?.product;
      } else {
        this.showModal = false;
        this.selectOptions = null;
        this.index = null;
        this.product = null;
      }
    });
  },
  beforeDestroy() {
    this.$bus.$off("select-product-variable-modal");
  },
};
</script>
